import "./AboutMe.css";
import React from "react";
import Header from "../Header/Header";
import myphoto from "../../media/myphoto.jfif";
import reactVector from "../../media/react.png";
import nodeVector from "../../media/node.svg";
import expressVector from "../../media/express.png";
import mongodbVector from "../../media/mongodb.png";
import { menuOption } from "../../actions";
import { connect } from "react-redux";

const formProps = {
  title: "About Me",
};

class AboutMe extends React.Component {
  render() {
    return (
      <div>
        <Header />
        <div className="aboutme-container">
          <div style={{ paddingBottom: "50px", textAlign: "center" }}>
            <h1>{formProps.title}</h1>
          </div>
          <div className="aboutme-content-container">
            <div className="ui two item menu aboutme-menubar">
              <div
                className={`${this.props.menuOptionState.option0ClassName} item aboutme-menubar-items`}
                onClick={() => this.props.menuOption("0", "active", "")}
              >
                Who / What / How ?
              </div>
              <div
                className={`${this.props.menuOptionState.option1ClassName} item aboutme-menubar-items`}
                onClick={() => this.props.menuOption("1", "", "active")}
              >
                Technology Stack I use
              </div>
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
              }}
              className="content"
            >
              <div>
                <img
                  style={{
                    height: "auto",
                    width: "100%",
                    maxWidth: "300px",
                    minWidth: "300px",
                  }}
                  src={myphoto}
                  className="ui circular image aboutme-image"
                  alt=""
                />
              </div>
              <div className="description aboutme-description">
                {this.props.menuOptionState.selectedMenuOption === "0" ? (
                  <p className="aboutme-description-p">
                    My name is Gustavo Rios, I'm a{" "}
                    <strong>full-stack web developer</strong>. I create{" "}
                    <strong>beautiful web applications</strong> with the most
                    trending technologies on the webdev market. My approach on
                    every project always take into account{" "}
                    <strong>user experience</strong> and how every step I take
                    affects said experience, with that in mind{" "}
                    <strong>
                      app responsiveness with mobile-first design is a top
                      priority
                    </strong>
                    , I sum up that with a great sense of style and attention to
                    detail, keeping the page{" "}
                    <strong>loyal to your branding</strong> to deliver a{" "}
                    <strong>product of top quality</strong>.
                  </p>
                ) : (
                  <div className="techstack-image-container">
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "center",
                      }}
                    >
                      <div
                        style={{ border: "none" }}
                        className="ui circular image"
                      >
                        <div>
                          <img
                            className="techstack-logos"
                            src={mongodbVector}
                            alt="mongodb"
                          />
                        </div>
                      </div>
                      <div style={{ margin: "10px", fontSize: "2em" }}>
                        MongoDB
                      </div>
                    </div>
                    <div style={{ fontSize: "2em", margin: "10px" }}>+</div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "center",
                      }}
                    >
                      <div
                        style={{ border: "none" }}
                        className="ui circular image"
                      >
                        <div>
                          <img
                            className="techstack-logos"
                            src={expressVector}
                            alt="express"
                          />
                        </div>
                      </div>
                      <div style={{ margin: "10px", fontSize: "2em" }}>
                        Express.js
                      </div>
                    </div>
                    <div style={{ fontSize: "2em", margin: "10px" }}>+</div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "center",
                      }}
                    >
                      <div
                        style={{ border: "none" }}
                        className="ui circular image"
                      >
                        <div>
                          <img
                            style={{
                              animation: "rotation 10s infinite linear",
                            }}
                            className="techstack-logos"
                            src={reactVector}
                            alt="react"
                          />
                        </div>
                      </div>
                      <div style={{ margin: "10px", fontSize: "2em" }}>
                        ReactJs
                      </div>
                    </div>
                    <div style={{ fontSize: "2em", margin: "10px" }}>+</div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "center",
                      }}
                    >
                      <div
                        style={{ border: "none" }}
                        className="ui circular image"
                      >
                        <div>
                          <img
                            className="techstack-logos"
                            src={nodeVector}
                            alt="redux"
                          />
                        </div>
                      </div>
                      <div style={{ margin: "10px", fontSize: "2em" }}>
                        Node
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { menuOptionState: state.menuOption };
};

export default connect(mapStateToProps, { menuOption })(AboutMe);
