export default ( state = null, action ) => {
    switch (action.type) {
        case 'CHANGE_OPTION': 
            return { 
                ...state, 
                selectedMenuOption: action.payload.selectedMenuOption, 
                option0ClassName: action.payload.option0ClassName, 
                option1ClassName: action.payload.option1ClassName
                };
        default: 
            return { 
                ...state, 
                selectedMenuOption: '0', 
                option0ClassName: 'active', 
                option1ClassName: ''
                };
    }
}