import "./ImageGrid.css";
import React from "react";

const pE = "/admin/project-edit";
const pD = "/admin/project-delete";

class ImageGrid extends React.Component {
  renderProjects = () => {
    if (!this.props.projects) {
      let arr =
        window.location.pathname === pE || window.location.pathname === pD
          ? new Array(2)
          : new Array(3);
      for (let i = 0; i < arr.length; i++) arr[i] = i;

      return arr.map((element) => {
        return (
          <div
            key={element}
            style={{ margin: "15px", background: "#2f4f4f" }}
            className="ui inverted segment card column column-item"
          >
            <div
              style={{ height: "250px", width: "190px" }}
              className="content"
            >
              <div
                className="ui active inverted loader"
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              ></div>
            </div>
          </div>
        );
      });
    } else {
      return this.props.projects.map((project) => {
        return (
          <div
            style={{ margin: "15px", background: "#2f4f4f" }}
            key={project._id}
            className="ui inverted segment card column column-item"
          >
            <div className="content content-container">
              <h3>{project.projectName}</h3>
            </div>
            <div className="image-container">
              <div>
                <img
                  id={`project-logo${project._id}`}
                  key={project._id}
                  src={project.img}
                  alt=""
                />
              </div>
            </div>
            <div className="content-container">
              {this.props.formProps.gridTitle === "Portfolio" ? (
                <div className="description">
                  <p>{project.projectDescription}</p>
                </div>
              ) : null}
            </div>
            {!this.props.formProps.gridCardButton ? null : (
              <div className="content button-container">
                <div
                  className="ui button"
                  onClick={() => {
                    switch (this.props.formProps.gridCardButton) {
                      case "Edit":
                        return this.props.onEditClick(project);
                      case "Delete":
                        return this.props.onDeleteClick(project);
                      case "Open App":
                        return this.props.onOpenAppClick(project.url);
                      default:
                        return null;
                    }
                  }}
                  style={{ background: "#f0db4f" }}
                  href={project.url}
                >
                  {this.props.formProps.gridCardButton}
                </div>
              </div>
            )}
          </div>
        );
      });
    }
  };

  render() {
    return (
      <div className="grid-container">
        <div style={{ paddingBottom: "50px", textAlign: "center" }}>
          <h1>{this.props.formProps.gridTitle}</h1>
        </div>
        <div
          className="ui stackable six column grid"
          style={{ display: "flex", justifyContent: "center" }}
        >
          {this.renderProjects()}
        </div>
      </div>
    );
  }
}

export default ImageGrid;
