import React from "react";
import ProjectForm from "../ProjectForm/ProjectForm";
import ImageGrid from "../ImageGrid/ImageGrid";
import { reduxForm } from "redux-form";
import { change } from "redux-form";
import { connect } from "react-redux";
import { fetchProjects, editProject } from "../../../actions";

const formProps = {
  gridTitle: "Select a Project to Edit",
  gridCardButton: "Edit",
  title: "Edit a Project",
  label_1: "Project Name",
  label_2: "Description",
  label_3: "Url",
  button: "Submit Changes",
};

let imgSrc = "";
let showForm = false;
let currentProjectId = "";

class ProjectEdit extends React.Component {
  componentDidMount() {
    this.props.fetchProjects();
  }

  componentWillUnmount() {
    showForm = false;
  }

  renderInput = ({ input }) => {
    if (input.name === "projectName") {
      return (
        <div className="field">
          <label>{formProps.label_1}</label>
          <input {...input} />
        </div>
      );
    }

    if (input.name === "projectDescription") {
      return (
        <div className="field">
          <label>{formProps.label_2}</label>
          <textarea {...input} />
        </div>
      );
    }

    if (input.name === "url") {
      return (
        <div className="field">
          <label>{formProps.label_3}</label>
          <input {...input} />
        </div>
      );
    }

    if (input.name === "img") {
      return <input style={{ display: "none" }} {...input} />;
    }
  };

  onEditClick = (project) => {
    showForm = true;
    imgSrc = project.img;
    currentProjectId = project._id;
    this.props.dispatch(
      change("projectEdit", "projectName", project.projectName)
    );
    this.props.dispatch(
      change("projectEdit", "projectDescription", project.projectDescription)
    );
    this.props.dispatch(change("projectEdit", "url", project.url));
  };

  onImageUpload = (ImgEnc) => {
    this.props.dispatch(change("projectEdit", "img", ImgEnc));
  };

  onSubmit = (formValues) => {
    this.props.editProject(formValues, currentProjectId);
    this.props.dispatch(change("projectEdit", "projectName", ""));
    this.props.dispatch(change("projectEdit", "projectDescription", ""));
    this.props.dispatch(change("projectEdit", "url", ""));
    document.querySelector("#submittedImageEdit")
      ? document.querySelector("#submittedImageEdit").remove()
      : document.querySelector("#submittedImage").remove();
    showForm = false;
  };

  actImg = () => {
    return (
      <img
        id="submittedImageEdit"
        src={imgSrc}
        style={{ width: "100%", maxWidth: "160px" }}
        alt=""
      />
    );
  };

  render() {
    if (!showForm) {
      return (
        <ImageGrid
          formProps={formProps}
          onEditClick={this.onEditClick}
          projects={this.props.projects}
        />
      );
    } else {
      return (
        <form
          encType="multipart/form-data"
          className="ui form"
          onSubmit={this.props.handleSubmit(this.onSubmit)}
        >
          <h2 style={{ paddingTop: "30px" }}>{formProps.title}</h2>
          <ProjectForm
            showForm={showForm}
            imgScr={imgSrc}
            actImg={this.actImg}
            onImageUpload={this.onImageUpload}
            renderInput={this.renderInput}
            formProps={formProps}
          />
        </form>
      );
    }
  }
}

const mapStateToProps = (state) => {
  return { projects: state.projects.projects };
};

export default connect(mapStateToProps, { fetchProjects, editProject })(
  reduxForm({ form: "projectEdit" })(ProjectEdit)
);
